import React, { useState } from "react";
import styles from "../UI/Card.module.css";
import Card from "../UI/Card";
import Button from "../UI/Button";
import ErrorModal from "../UI/ErrorModal";
import "./InputNotes.css";

const InputNotes = (props) => {
  const [enteredNote, setEnteredNote] = useState("");
  const [enteredName, setEnteredName] = useState("");
  const [error, setError] = useState();

  const noteInputHendler = (event) => {
    event.preventDefault();
    if (enteredNote.trim().length === 0 || enteredName.trim().length === 0) {
      setError({
        title: "Что-то пошло не так",
        message: "Все строки должны быть заполнены!",
      });
      return;
    }
    props.onAddSpeakers(enteredNote, enteredName);
    setEnteredNote(""); // когда выполныется эта функция то наше второе значение превращется в пустцю строку
    setEnteredName("");
  };

  const noteChangeHandler = (event) => {
    setEnteredNote(event.target.value);
  };

  const nameChangeHandler = (event) => {
    setEnteredName(event.target.value);
  };

  const errorHeandler = () => {
    setError(null);
  };

  return (
    <div className="box">
      {error && (
        <ErrorModal
          title={error.title}
          message={error.message}
          onHeandlerError={errorHeandler}
        />
      )}
      <Card className={styles.card}>
        <form onSubmit={noteInputHendler}>
          <label htmlFor="name">Название</label>
          <input
            id="name"
            type="text"
            value={enteredName}
            onChange={nameChangeHandler}
          />{" "}
        </form>

        <form onSubmit={noteInputHendler}>
          <label htmlFor="note">Заметка</label>
          <input
            id="note"
            type="text"
            value={enteredNote}
            onChange={noteChangeHandler}
          />
          <Button type="submit">Добавить</Button>
        </form>
      </Card>
    </div>
  );
};
export default InputNotes;
