// это програмка заметок
import React, { useState } from "react";
import InputNotes from "./components/InputNote/InputNotes";
import Note from "./components/Note/Note";

import "./App.css";

const App = () => {
  const [newUser, setNewUser] = useState([]);

  const addNewUsersHandler = (newName, newNote) => {
    setNewUser((prevPosition) => {
      return [...prevPosition, { name: newName, note: newNote }];
    });
  };
  return (
    <div className="App">
      <InputNotes onAddSpeakers={addNewUsersHandler} />
      <Note />
    </div>
  );
};

export default App;
